<template>
  <card :title="title" :isLoading="isLoading">
    <template #tools>
      <div class="btn-group btn-group-toggle" data-toggle="buttons">
        <label
          v-for="language in languages"
          :key="language.id"
          class="btn btn-secondary"
          :class="{ active: language.checked }"
        >
          <input
            type="radio"
            name="options"
            id="option_a1"
            autocomplete="off"
            :checked="language.checked"
          />
          {{ language.name }}
        </label>
      </div>
    </template>
    <template #body>
      <form action="">
        <div class="form-group">
          <label for="filmName">Назва фільма</label>
          <input-text></input-text>
          <input
            type="text"
            class="form-control"
            id="filmName"
            placeholder="Введіть назву фільма..."
          />
        </div>
        <div class="form-group">
          <label for="infoArea">Опис</label>
          <textarea
            class="form-control"
            rows="3"
            id="infoArea"
            placeholder="Опис фільма..."
          ></textarea>
        </div>
        <label>Головна картинка</label>
        <list-img
          :eOne="true"
          sizeImg="1000x1000"
          :collectionRef="collectionRef"
          fileFolderPath="films/id/2/"
          :saveListImgTriger="saveListImgTriger"
          :updateListImgTriger="updateListImgTriger"
          @isLoading="changeLoading"
        ></list-img>
        <label>Галерея картинок</label>
        <list-img
          :eOne="false"
          sizeImg="1000x190"
          :collectionRef="collectionRef"
          fileFolderPath="films/id/2/"
          :saveListImgTriger="saveListImgTriger"
          :updateListImgTriger="updateListImgTriger"
          @isLoading="changeLoading"
        ></list-img>
        <div class="form-group">
          <label for="linkInput">Силка на трейлер</label>
          <input
            type="text"
            class="form-control"
            id="linkInput"
            placeholder="Введіть силку на трейлер в youtube..."
          />
        </div>
        <div class="form-group">

          <div class="form-check">
            <input class="form-check-input" type="checkbox" />
            <label class="form-check-label">3D</label>
          </div>
          <div class="form-check">
            <input class="form-check-input" type="checkbox" />
            <label class="form-check-label">2D</label>
          </div>
          <div class="form-check">
            <input class="form-check-input" type="checkbox" />
            <label class="form-check-label">IMAX</label>
          </div>
        </div>
        <div class="form-group">
          <label>SEO блок</label>
          <div class="form-group row">
            <label for="inputURL" class="col-sm-2  col-form-label"
              >URL:</label
            >
            <div class="col-sm-10">
              <input
                type="email"
                class="form-control"
                id="inputURL"
                placeholder="URL"
              />
            </div>
          </div>
          <div class="form-group row">
            <label for="inputURL" class=" col-sm-2 col-form-label"
              >Title:</label
            >
            <div class="col-sm-10">
              <input
                type="email"
                class="form-control"
                id="inputURL"
                placeholder="Title"
              />
            </div>
          </div>
          <div class="form-group row">
            <label for="inputURL" class="col-sm-2  col-form-label"
              >Keywords:</label
            >
            <div class="col-sm-10">
              <input
                type="email"
                class="form-control"
                id="inputURL"
                placeholder="Keywords"
              />
            </div>
          </div>
          <div class="form-group row">
            <label for="inputURL" class="col-sm-2 col-form-label"
              >Description:</label
            >
            <div class="col-sm-10">
              <input
                type="email"
                class="form-control"
                id="inputURL"
                placeholder="Description"
              />
            </div>
          </div>
        </div>
      </form> </template
  >
  <template #footer>
      <span class="btn btn-xs btn-success col fileinput-button" @click="Save">
        <span>Зберегти</span>
      </span>
    </template></card>
</template>

<script>
import card from '@/components/UI/card-component.vue'
import { filmsRef, filmsGallaryRef } from '@/js/collections'
import listImg from '../list-img/list-img.vue'
import inputText from '../UI/inputs/input-text.vue'
export default {
  data () {
    return {
      title: 'Фільм',
      isLoading: false,
      fileFolderPath: 'films/id/2/',
      filmsGallaryRef,
      collectionRef: filmsRef,
      saveListImgTriger: false,
      updateListImgTriger: false,
      eOne: true,
      languages: [
        { id: 0, name: 'Українська', checked: true },
        { id: 1, name: 'Англійська', checked: false },
        { id: 2, name: 'Іспанська', checked: false }
      ]
    }
  },
  components: {
    card,
    listImg,
    inputText
  },
  methods: {
    changeLoading (value) {
      this.isLoading = value
    }
  }
}
</script>

